
input[type="text"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
}
label {
  font-weight: bold;
}
.note {
  color: red;
}
#dropzone {
  /* overflow-y: scroll; */
  height: 180px;
  position: relative;
  overflow-y: scroll;
}
#dropzone::-webkit-scrollbar-track {
    background-color: #F5F5F5;
}
#dropzone::-webkit-scrollbar {
    width: 8px;
    background-color: #F5F5F5;
}
#dropzone::-webkit-scrollbar-thumb {
    background-color: #888;
}



